:root {
    --border-size: 3px;
}

* {
    scrollbar-width: thin;
    scrollbar-color: hsla(0, 0%, 0%, 48%) hsla(0, 0%, 0%, 18%);
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background-color: hsla(0, 0%, 0%, 18%);
}

::-webkit-scrollbar-thumb {
    background-color: hsla(0, 0%, 0%, 48%);
}

::-webkit-scrollbar-corner {
    background-color: hsla(0, 0%, 0%, 48%);
}

body {
    margin: 0;

    font-family: Nunito, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    min-block-size: 100vh;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0;
    padding: 0;
}

button {
    padding: 0.46rem 1rem;
    border-radius: 0.28rem;
    border: var(--border-size) solid hsl(0, 0%, 0%);
    font-weight: 600;
    text-transform: uppercase;
    cursor: pointer;
}

button:hover {
    background-color: hsla(0, 0%, 80%);
    border-color: hsla(0, 0%, 0%);
}

:disabled {
    pointer-events: none;
    user-select: none;
}

input {
    padding: 0.45rem;
    border-radius: 0.28rem;
    border: var(--border-size) solid black;
}

.tool-btn {
    padding: 0px 3.5px !important;
    border-radius: 999px;
    border: none;
}

.sidebar {
    padding-inline: 0.64rem;
    border-inline-end: var(--border-size) solid hsl(0, 0%, 0%);
    min-block-size: 100vh;
    position: fixed;
    inline-size: 300px;
}

.sidebar h2 {
    margin-block-end: 0.64rem;
}

.sidebar .options {
    display: flex;
    justify-content: space-between;
    gap: 0.64rem;
}

.sidebar .add-form {
    display: flex;
    gap: 1.5rem;
    align-items: flex-end;
    margin-block-start: 1rem;
    padding-block-end: 1rem;

    border-block-end: var(--border-size) solid hsl(0, 0%, 0%);
}

.sidebar .add-form label input {
    width: 100%;
}

.sidebar .usernames {
    overflow: auto;
    padding-inline: 1rem;
    block-size: calc(100vh - 174px);
}

.sidebar .usernames ol {
    padding-inline-start: 0;
}

.sidebar .usernames ol li {
    padding-block: 0.25rem;

    display: flex;
    justify-content: space-between;
}

.sidebar .usernames ol li span {
    cursor: pointer;
    flex: 1;
}

.sidebar .usernames ol li span::before {
    content: "@";
    margin-inline-end: 0.08rem;
}

.content-area {
    margin-inline-start: 320px;
    padding: 1rem;
}

.content-area .header {
    display: flex;
    justify-content: space-between;
}

.content-area .feeds {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-end;
    gap: 0.64rem;
    margin-block-start: 1rem;
    padding-block-start: 1rem;
    border-block-start: var(--border-size) solid hsl(0, 0%, 0%);
}

.content-area .feeds article {
    display: grid;
    grid-template-rows: 1fr auto;

    box-shadow: 0 2px 3px 1px hsla(0, 0%, 0%, 25%);
    border-radius: 16px;
    inline-size: min-content;
    min-inline-size: 320px;
    overflow: hidden;
    block-size: fit-content;
}

.content-area .feeds article .body {
    padding: 0.64rem;
    line-height: 1.5rem;
}

.content-area .feeds article img,
.content-area .feeds article video {
    max-inline-size: 100%;
}
